#credits {
    width: 80vw;
    max-width: 1000px;
    text-align: center;
}

#credits-div {
    margin-top: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack:center;
        justify-content:center;
}

#credits-div > * {
    margin: 0 10px;
}

#credits-div img{
    width: 40px;
}

#credits ul {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-align: center;
       align-items: center;
   -ms-flex-pack: distribute;
       justify-content: space-around;
   /* You can set flex-wrap and
      flex-direction individually */
   -ms-flex-direction: row;
       flex-direction: row;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
   /* Or do it all in one line
     with flex flow */
   -ms-flex-flow: row wrap;
       flex-flow: row wrap;
   /* tweak where items line
      up on the row
      valid values are: flex-start,
      flex-end, space-between,
      space-around, stretch */
   -ms-flex-line-pack: end;
       align-content: flex-end;
}

#credits li {
    margin: 0 10px;
    max-width: 100%;
    font-size: 0.9em;
    line-height: 2em;
}

#credits a {
    margin: 0 10px;
    max-width: 100%;
}


